<template>
  <div>
    <Login v-if="!islogged"/>
    <Chat v-else/>
  </div>
</template>

<script>
import axios from "axios";
import Login from "@/views/Login";
import Chat from "@/views/Chat";
export default {
  name: "Home",
  components: {
    Chat,
    Login
  },
  data() {
    return{
      islogged: false
    }
  },
  methods: {
    checkLogged(){
      let vm = this
      axios.get(this.$store.state.request_url + 'user/is-logged')
          .then(function (response) {
           vm.islogged = response.data
          })
          .catch(function (error) {
            console.log(error);
          })
          .then(function () {
          //  vm.islogged = false
          });

    }
  },
  mounted() {
    let vm = this
      setInterval(function (){
        vm.checkLogged()
          //  console.log(vm.islogged)
          }, 500
      )
  }
}
</script>

